'use client'

import { useState, useEffect, useRef } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Search, Menu, X, Loader2 } from 'lucide-react'
import { ThemeToggle } from './theme-toggle'
// Import tipe dan helper API yang sudah dibuat sebelumnya
// import { searchAnime, Anime } from '@/lib/api' 

// --- MOCK DATA & FUNCTION (Hapus bagian ini jika sudah connect API asli) ---
const searchAnime = async (query, page, signal) => {
  await new Promise(resolve => setTimeout(resolve, 500)); // Fake delay
  if (signal?.aborted) return { data: [] };
  
  // Mock results
  return {
    data: [
      { mal_id: 1, title: `Result for ${query} 1`, type: 'TV', episodes: 12, images: { jpg: { image_url: 'https://placehold.co/50x70', large_image_url: 'https://placehold.co/50x70' } } },
      { mal_id: 2, title: `Result for ${query} 2`, type: 'Movie', episodes: 1, images: { jpg: { image_url: 'https://placehold.co/50x70', large_image_url: 'https://placehold.co/50x70' } } },
      { mal_id: 3, title: `Result for ${query} 3`, type: 'TV', episodes: 24, images: { jpg: { image_url: 'https://placehold.co/50x70', large_image_url: 'https://placehold.co/50x70' } } },
    ]
  }
};
// --------------------------------------------------------------------------

export function Navbar() {
  const router = useRouter()
  const [isOpen, setIsOpen] = useState(false)
  const [mobileSearchOpen, setMobileSearchOpen] = useState(false)
  
  // State Search
  const [searchQuery, setSearchQuery] = useState('')
  const [results, setResults] = useState([])
  const [loading, setLoading] = useState(false)
  
  // Refs
  const dropdownRef = useRef(null)
  const abortControllerRef = useRef(null)

  // --- LOGIC PENCARIAN OPTIMIZED ---
  useEffect(() => {
    if (!searchQuery.trim()) {
      setResults([])
      setLoading(false)
      return
    }

    if (abortControllerRef.current) {
      abortControllerRef.current.abort()
    }

    const controller = new AbortController()
    abortControllerRef.current = controller
    setLoading(true)

    const timeoutId = setTimeout(async () => {
      try {
        const res = await searchAnime(searchQuery, 1, controller.signal)
        if (!controller.signal.aborted) {
          setResults(res.data?.slice(0, 6) || [])
          setLoading(false)
        }
      } catch (error) {
        if (error.name !== 'AbortError') {
          console.error("Navbar search error:", error)
          setResults([])
          setLoading(false)
        }
      }
    }, 500)

    return () => {
      clearTimeout(timeoutId)
      controller.abort()
    }
  }, [searchQuery])

  // Close dropdown when clicking outside
  useEffect(() => {
    const listener = (e) => {
      if (dropdownRef.current && !dropdownRef.current.contains(e.target)) {
        setResults([])
      }
    }
    document.addEventListener('mousedown', listener)
    return () => document.removeEventListener('mousedown', listener)
  }, [])

  const handleSubmitSearch = (e) => {
    e?.preventDefault()
    if (!searchQuery) return
    setResults([])
    setMobileSearchOpen(false)
    setIsOpen(false)
    router.push(`/search?q=${encodeURIComponent(searchQuery)}`)
  }

  // Class untuk item menu desktop agar rapi
  // text-muted-foreground: warna abu-abu (default)
  // hover:text-primary: warna hitam/putih solid (saat hover)
  // hover:bg-primary/10: background transparan warna primary (saat hover)
  const navLinkClass = "px-4 py-2 rounded-full text-sm font-medium text-muted-foreground hover:text-primary hover:bg-primary/10 transition-all duration-200 ease-in-out"

  return (
    <>
      {/* NAVBAR */}
      <nav className="sticky top-0 z-50 bg-card/95 backdrop-blur border-b border-border">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">

            {/* Logo */}
            <Link href="/" className="flex items-center gap-2 group">
              <div className="w-8 h-8 rounded-full overflow-hidden flex items-center justify-center border border-border">
                <img
                  src="/feinime.jpg" 
                  alt="Logo"
                  className="w-full h-full object-cover"
                  onError={(e) => {
                    e.target.onerror = null;
                    e.target.src = "https://placehold.co/40x40?text=F"
                  }}
                />
              </div>
              <span className="text-xl font-bold text-foreground hidden sm:inline group-hover:text-primary transition-colors">
                Feinime
              </span>
            </Link>

            {/* Desktop Menu */}
            {/* UPDATE: Menggunakan variable navLinkClass untuk styling yang konsisten dan kontras */}
            <div className="hidden md:flex items-center gap-2">
              <Link href="/" className={navLinkClass}>Home</Link>
              <Link href="/trending" className={navLinkClass}>Trending</Link>
              <Link href="/seasonal" className={navLinkClass}>Seasonal</Link>
              <Link href="/popular" className={navLinkClass}>Popular</Link>
              <Link href="/genres" className={navLinkClass}>Genres</Link>
            </div>

            {/* Desktop Search */}
            <div className="relative hidden sm:flex items-center gap-2 bg-input border border-border rounded-full px-4 py-2 w-64 focus-within:ring-2 focus-within:ring-primary/20 transition-all">
              <Search size={18} className="text-muted-foreground" />
              <form onSubmit={handleSubmitSearch} className="flex-1">
                <input
                  type="text"
                  placeholder="Search anime..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="bg-transparent outline-none text-sm w-full placeholder:text-muted-foreground text-foreground"
                />
              </form>

              {loading && <Loader2 size={16} className="animate-spin text-primary" />}

              {/* Dropdown Desktop */}
              {results.length > 0 && (
                <div
                  ref={dropdownRef}
                  className="absolute top-full left-0 w-full mt-4 bg-card border border-border rounded-xl shadow-2xl overflow-hidden z-50 animate-in fade-in slide-in-from-top-2"
                >
                  {results.map((anime) => (
                    <Link
                      key={anime.mal_id}
                      href={`/anime/${anime.mal_id}`}
                      onClick={() => {
                        setResults([])
                        setSearchQuery('')
                      }}
                      className="flex items-center gap-3 px-4 py-3 w-full text-left hover:bg-primary/5 transition-colors border-b border-border/50 last:border-0 group"
                    >
                      <img
                        src={anime.images.jpg.image_url || anime.images.jpg.large_image_url}
                        alt={anime.title}
                        className="w-10 h-14 object-cover rounded-md shadow-sm group-hover:scale-105 transition-transform"
                      />
                      <div className="flex-1 min-w-0">
                        <p className="text-sm font-medium truncate text-foreground group-hover:text-primary transition-colors">{anime.title}</p>
                        <p className="text-xs text-muted-foreground">{anime.type} • {anime.episodes ?? '?'} eps</p>
                      </div>
                    </Link>
                  ))}
                  <button 
                    onClick={() => handleSubmitSearch()}
                    className="w-full text-center text-xs font-bold uppercase tracking-wider text-primary bg-primary/5 py-3 hover:bg-primary/10 transition-colors"
                  >
                    View all results
                  </button>
                </div>
              )}
            </div>

            {/* Right (Theme + Mobile) */}
            <div className="flex items-center gap-2">
              {/* Mobile Search Button */}
              <button
                className="sm:hidden p-2 text-muted-foreground hover:text-primary hover:bg-primary/10 rounded-full transition-colors"
                onClick={() => setMobileSearchOpen(true)}
              >
                <Search size={22} />
              </button>

              <ThemeToggle />

              {/* Mobile Menu Button */}
              <button
                className="md:hidden p-2 text-muted-foreground hover:text-primary hover:bg-primary/10 rounded-full transition-colors"
                onClick={() => setIsOpen(!isOpen)}
              >
                {isOpen ? <X size={24} /> : <Menu size={24} />}
              </button>
            </div>
          </div>

          {/* MOBILE MENU */}
          {isOpen && (
            <div className="md:hidden mt-2 space-y-1 px-2 pb-4 animate-in slide-in-from-top-5 border-t border-border/50 pt-4">
              <Link href="/" onClick={() => setIsOpen(false)} className="block px-4 py-3 rounded-lg text-muted-foreground hover:text-primary hover:bg-primary/10 transition-colors font-medium">Home</Link>
              <Link href="/trending" onClick={() => setIsOpen(false)} className="block px-4 py-3 rounded-lg text-muted-foreground hover:text-primary hover:bg-primary/10 transition-colors font-medium">Trending</Link>
              <Link href="/seasonal" onClick={() => setIsOpen(false)} className="block px-4 py-3 rounded-lg text-muted-foreground hover:text-primary hover:bg-primary/10 transition-colors font-medium">Seasonal</Link>
              <Link href="/popular" onClick={() => setIsOpen(false)} className="block px-4 py-3 rounded-lg text-muted-foreground hover:text-primary hover:bg-primary/10 transition-colors font-medium">Popular</Link>
              <Link href="/genres" onClick={() => setIsOpen(false)} className="block px-4 py-3 rounded-lg text-muted-foreground hover:text-primary hover:bg-primary/10 transition-colors font-medium">Genres</Link>
            </div>
          )}
        </div>
      </nav>

      {/* FULLSCREEN MOBILE SEARCH */}
      {mobileSearchOpen && (
        <div className="fixed inset-0 bg-background/95 backdrop-blur-xl z-[999] p-4 animate-in fade-in duration-200">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-lg font-bold text-foreground">Search Anime</h2>
            <button
              onClick={() => { setMobileSearchOpen(false); setResults([]); setSearchQuery('') }}
              className="p-2 rounded-full hover:bg-secondary transition-colors"
            >
              <X size={24} />
            </button>
          </div>

          <div className="flex items-center gap-3 bg-input border border-border rounded-2xl px-4 py-3 focus-within:ring-2 focus-within:ring-primary/20 transition-all shadow-sm">
            <Search size={20} className="text-muted-foreground" />
            <form onSubmit={handleSubmitSearch} className="flex-1">
              <input
                autoFocus
                type="text"
                placeholder="Type to search..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="bg-transparent outline-none text-lg w-full text-foreground placeholder:text-muted-foreground"
              />
            </form>
            {loading && <Loader2 className="animate-spin text-primary" size={20} />}
          </div>

          <div className="mt-6 space-y-3 max-h-[70vh] overflow-y-auto no-scrollbar">
            {results.length === 0 && searchQuery && !loading && (
               <div className="text-center mt-10 opacity-50">
                  <p>No results found for "{searchQuery}"</p>
               </div>
            )}

            {results.map((anime) => (
              <Link
                key={anime.mal_id}
                href={`/anime/${anime.mal_id}`}
                onClick={() => {
                  setMobileSearchOpen(false)
                  setResults([])
                  setSearchQuery('')
                }}
                className="flex items-center gap-4 p-3 rounded-xl hover:bg-primary/5 active:bg-primary/10 w-full text-left transition-colors animate-in slide-in-from-bottom-2"
              >
                <img
                  src={anime.images.jpg.image_url || anime.images.jpg.large_image_url}
                  alt={anime.title}
                  className="w-14 h-20 object-cover rounded-lg shadow-sm"
                />
                <div>
                  <p className="font-semibold line-clamp-2 text-foreground">{anime.title}</p>
                  <p className="text-xs text-muted-foreground mt-1 bg-secondary/50 self-start inline-block px-2 py-0.5 rounded">{anime.type} • {anime.episodes ?? '?'} eps</p>
                </div>
              </Link>
            ))}
            
            {results.length > 0 && (
               <button 
                onClick={() => handleSubmitSearch()}
                className="w-full py-3 mt-4 text-center text-primary font-bold bg-primary/5 rounded-xl hover:bg-primary/10 transition-colors"
               >
                 See all results
               </button>
            )}
          </div>
        </div>
      )}
    </>
  )
}